@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;300;400;500;700&display=swap);
.app{padding:20px 0}

.search{border-radius:5px}.search .search-form{padding:20px}.search .search-form .search-input{width:100%}.search .search-form .search-input input{border-right:none;font-family:"Roboto Mono",Helvetica,Arial,sans-serif;font-weight:300}.search .search-form .search-input button.search-button{font-weight:300;font-family:"Roboto Mono",Helvetica,Arial,sans-serif;text-transform:lowercase}.loading{font-weight:300;padding:20px 10px}.stats{font-size:0.8em;margin:20px 0 0 0;padding:5px 10px;font-weight:300}.results{font-family:"Roboto Mono",Helvetica,Arial,sans-serif;font-weight:300}.no-results{padding:20px;border-radius:5px;margin-top:20px}i.scroll{background:#333;color:#fff;padding:5px;height:30px;width:30px;border-radius:5px;opacity:0.85;font-size:18px}@media only screen and (max-width: 768px){.search .search-form .ui.icon.input>i.icon:before{margin-top:.25em}.search .search-form .search-input{display:inline;display:initial}.search .search-form .search-input input{width:100% !important}.search .search-form .search-input button.search-button{width:100%;margin-top:1em;border-radius:5px !important;text-align:center;display:block}.search .search-form .ui.action.input:not([class*="left action"])>input{border-top-right-radius:5px !important;border-top-left-radius:5px !important;border-bottom-right-radius:5px !important;border-bottom-left-radius:5px !important;border:1px solid #dfdfdf !important}}

.item{margin:20px 0;padding:20px;border-radius:5px}.item .exact-match{padding:0px 7px;background:#2085d0;border-radius:3px;color:#ffffff;display:inline-block;font-size:0.65em;font-family:"Open Sans",Helvetica,Arial,sans-serif;font-weight:300;margin-left:10px}.item h3{font-family:"Open Sans",Helvetica,Arial,sans-serif;font-weight:600;margin-bottom:10px}.item .version{font-family:"Roboto Mono",Helvetica,Arial,sans-serif;font-weight:400;color:#85b7d9;font-size:0.9em;margin-left:5px}.item .description{margin-top:20px}.item ul.keywords{margin:20px 0 0 0;padding:0}.item ul.keywords li{list-style-type:none;display:inline-block;background:#f5f5f5;padding:3px 7px;margin-right:10px;margin-top:5px;margin-bottom:5px;font-size:0.8em;border-radius:3px}.item ul.keywords li:last-child{margin-right:0}.item:hover{border-color:#85b7d9;cursor:pointer}

.details{margin:20px 0;padding:20px;border-radius:5px;font-family:"Roboto Mono",Helvetica,Arial,sans-serif;font-weight:300}.details .error{margin:1em 0}.details h3{font-family:"Open Sans",Helvetica,Arial,sans-serif;font-weight:600;margin-bottom:10px}.details h3.dependencies-heading{font-family:"Open Sans",Helvetica,Arial,sans-serif;font-weight:400;margin-top:30px;margin-bottom:30px;padding-bottom:10px;border-bottom:1px solid #f5f5f5}.details .version{font-weight:400;color:#85b7d9;font-size:0.8em;margin-left:5px}.details .description{margin-top:20px}.details ul.keywords{margin:20px 0 0 0;padding:0}.details ul.keywords li{list-style-type:none;display:inline-block;background:#f5f5f5;padding:3px 7px;margin-right:10px;margin-top:5px;margin-bottom:5px;font-size:0.8em;border-radius:3px}.details ul.keywords li:last-child{margin-right:0}

.dependecy-graph{margin:0;height:100%;border:1px solid #f5f5f5}.dependecy-graph .vis-network:focus{border:none;box-shadow:none;outline:none}

body{font:100% "Roboto Mono",Helvetica,Arial,sans-serif;margin:0;background-color:#f5f5f5;height:100%;width:100%}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}

